.nav-bar{
    display: flex;
    justify-content: space-between;
    background: hsla(0, 0%, 100%, 0.24);
    padding: 6px 0;
}
.nav-left{
    display: flex;
    margin-left: 6px;
}
.nav-right{
    display: flex;
    margin-right: 6px;
}
.board-menu{
    background: hsla(0, 0%, 100%, 0.24);
    padding: 9px 16px;
    border-radius: 2px;
}
.board-menu h4{
    font-size: 14px;
    margin: 0;
    font-weight: 600;
    font-family: sans-serif;
}
.board-menu:hover{
    background: hsla(0, 0%, 100%, 0.24);
    cursor: pointer;
}
.name-menu{
    margin-left: 6px;
    width:10%; 
}
.name-menu input{
    outline: none;
    border: none;
    text-align: center;
    width: 100%;
    padding: 6px;
    font-weight: 600;
    background-color: transparent;
    font-size: 19px;
    border-radius: 2px;
    font-family: sans-serif;
}
.name-menu input:hover{
    background: hsla(0, 0%, 100%, 0.24);
    cursor: pointer;
}
