body {
    margin: 0;
  }
  .menu-nav {
    background: #026aa7;
    padding: 5px 5px 2px 5px;
    display: flex;
    justify-content: space-between;
  }
  .listicon {
    color: white;
    background-color: #4e97c2;
    padding: 4px;
    border-radius: 4px;
  }
  .listicon:hover {
    cursor: pointer;
    background-color: hsla(0, 0%, 100%, 0.2);
  }
  .homeicon {
    padding: 4px;
    border-radius: 4px;
    margin-left: 5px;
    color: white;
  }
  .homeicon:hover {
    cursor: pointer;
    background-color: hsla(0, 0%, 100%, 0.2);
  }
  .dashboardicon {
    background-color: #4e97c2;
    padding: 4px;
    border-radius: 4px;
    margin-left: 5px;
    color: white;
  }
  .dashboardicon:hover {
    cursor: pointer;
    background-color: hsla(0, 0%, 100%, 0.2);
  }
  .searchicon {
    background-color: #4e97c2;
    padding: 4px 4px 4px 160px;
    border-radius: 4px;
    margin-left: 5px;
    color: white;
  }
  .searchicon:hover {
    background-color: hsla(0, 0%, 100%, 0.2);
  }
  .trello {
    margin: 0;
    padding: 6px;
    color: white;
    font-family: sans-serif;
    font-size: 20px;
  }
  .trello-logo {
    width: 8%;
    color: white;
  }
  .addicon {
    background-color: #4e97c2;
    padding: 4px;
    border-radius: 4px;
    color: white;
  }
  .addicon:hover {
    cursor: pointer;
    background-color: hsla(0, 0%, 100%, 0.2);
  }
  .infoicon {
    background-color: #4e97c2;
    padding: 4px;
    border-radius: 4px;
    margin-left: 5px;
    color: white;
  }
  .infoicon:hover {
    cursor: pointer;
    background-color: hsla(0, 0%, 100%, 0.2);
  }
  .notifyicon {
    background-color: #4e97c2;
    padding: 4px;
    border-radius: 4px;
    margin-left: 5px;
    color: white;
  }
  .notifyicon:hover {
    cursor: pointer;
    background-color: hsla(0, 0%, 100%, 0.2);
  }
  .avataricon {
    background-color: #4e97c2;
    padding: 4px;
    border-radius: 4px;
    margin-left: 5px;
    color: white;
  }
  .avataricon:hover {
    cursor: pointer;
    background-color: hsla(0, 0%, 100%, 0.2);
  }
  .menu-body {
    display: flex;
  }
  .menu-body-left {
    margin-left: 10%;
  }
  .menu-body-right {
    margin-left: 10%;
  }
  .menu-option {
    list-style: none;
    margin-top: 10px;
    padding: 8px;
    font-weight: 600;
    font-family: sans-serif;
  }
  .menu-option:hover {
    background-color: lightgrey;
    cursor: pointer;
  }
  .menu-body-right {
    display: flex;
  }
  .card-link {
    margin-top: 20px;
    margin-left: 10px;
  }
  .card {
    width: 185px;
    position: relative;
    margin-left: 15px;
    margin-top: 15px;
  }
  .card-img {
    width: 100%;
  }
  .imgs {
    width: 100%;
  }
  .template {
    position: absolute;
    bottom: -10px;
    color: white;
    right: 10px;
  }
  